let hosts = "http://proxy-mainnet.doschain.org/insight";
// let hosts2 = "http://mainnet.doschain.org/insight";
if (window.location.host !== "games.doschain.org") {
  hosts = "http://35.220.198.141:8088";
  // hosts = "http://10.0.0.11:8088";
  // hosts = "http://10.0.0.15:8083";
  // hosts2 = "https://testnet.doschain.org/insight";
}

/**
 * @param {String} addr 钱包地址
 * @param {String} state 应用类别 0:待申购，1:已申购
 * @param {number} page 当前页码
 * @param {number} limit 一页显示的数量
 * @param {number} category 分类id
 * @description 首页请求dapp应用列表
 */
export function getDappList({ addr, state, page, limit, category }) {
  let queryStr = `${hosts}/v1/api/apps/${addr}/${state}/?page=${page}&limit=${limit}`;
  if (category) queryStr += `&category=${category}`;

  return window.demos.request({
    method: "GET",
    url: queryStr,
  });
}

/**
 *
 * @description 获取首页分类列表
 */
export function getCategorites() {
  return window.demos.request({
    method: "GET",
    url: `${hosts}/v1/api/category`,
  });
}

/**
 *
 * @description 获取首页开发应用的信息
 */
export function getDappDevInfo() {
  return window.demos.request({ method: "GET", url: `${hosts}/v1/api/state` });
}

/**
 *
 * @param {number} id 应用ID
 * @description 获取应用详情信息
 */
export function getDappInfo(id) {
  return window.demos.request({
    method: "GET",
    url: `${hosts}/v1/api/app/${id}`,
  });
}

/**
 *
 * @param {String} addr 钱包地址
 * @param {String} raw  待广播交易哈希
 * @param {number} id   应用ID
 * @param {String} gain 收益地址
 * @param {String} code 邀请矿池地址 (非必填)
 * @param {String} mail 邮箱地址
 */
export function fetchBuyDapp({ addr, rawtx, id, gain, code, mail }) {
  return window.demos.request({
    method: "POST",
    url: `${hosts}/v1/api/app/${addr}/pay`,
    data: {
      rawtx,
      id,
      gain,
      code,
      mail,
    },
  });
}

/**
 * @param {String} address 钱包地址
 * @description 获取订单记录
 */
export const getOrders = (address) => {
  return window.demos.request({
    method: "GET",
    url: `${hosts}/v1/api/orders/${address}`,
  });
};

/**
 * @param {String} address 钱包地址
 * @param {String} id 订单id
 * @description 获取订单详情
 */
export const getOrdersDetails = (address, id) => {
  return window.demos.request({
    method: "GET",
    url: `${hosts}/v1/api/order/${address}/${id}`,
  });
};
