import axios from "axios";
import { Toast } from "antd-mobile";

const instance = axios.create({
  baseURL: "/v1/api/",
  timeout: 10000,
});

const successHandler = (res) => {
  if (!res) {
    Toast.fail("请求出错", 1.5);
    return;
  }
  const {
    status,
    data: { ret: success, data = null },
  } = res;

  if (parseInt(status) === 200) {
    const handleResBody = {
      success,

      // 响应数据结构体Object
      data,
      // 响应消息String
    };

    if (!handleResBody.success) {
      Toast.fail(handleResBody.data, 1.5);
    }
    return handleResBody;
  }
};

const errorHandler = (error) => {
  return Promise.reject(error);
};

// 响应拦截器
instance.interceptors.response.use(successHandler, errorHandler);

// 请求拦截器
instance.interceptors.request.use(
  (config) => {
    if (config.data && config.method === "post") {
      config.headers["Content-Type"] = "application/x-www-form-urlencoded";
      let ret = "";
      for (let it in config.data) {
        ret +=
          encodeURIComponent(it) +
          "=" +
          encodeURIComponent(config.data[it]) +
          "&";
      }
      config.data = ret.substring(0, ret.length - 1);
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

export default instance;
