import React, {useState, useEffect, useContext} from "react";

const useNow = (_now, timer) => {
    const [now, setNow] = useState(_now);

    function interval(time) {
            clearInterval(timer);
            setNow(time);
            timer = setInterval(() => {
                time ++;
                setNow(time);
            }, 1000);
        }

    return [now, interval];
}

export default useNow;
