import React, { useState, useEffect, Fragment, useContext } from "react";
import { Link } from "react-router-dom";
import cx from "classnames";
import { basicURL } from "../../utils";
import {
  getDappList,
  getDappDevInfo,
  getCategorites,
} from "../../services/network";
import { Spin, CountDown, GlobalContext } from "../../components";
import styles from "./home.module.scss";
import useNow from "../../hooks/useNow";
let timer;

const Tag = ({ text, className, onTap = () => {} }) => {
  return (
    <button className={className} onClick={onTap}>
      {text}
    </button>
  );
};

const DevelopNotice = ({ data }) => (
  <div className={styles["home-dapp__developing"]}>
    {data?.name ? (
      <Fragment>
        <p>{`当前开发任务: ${data.name}`}</p>
        <p>{`预计完成：${data.time}`}</p>
      </Fragment>
    ) : (
      <p style={{ marginTop: "0.18rem" }}>暂无开发中Dapp</p>
    )}
  </div>
);

const DappCard = ({ data = {}, history, curTab, now }) => {
  const { id, name, logo, price, order_time, state } = data;

  return (
    <div className={styles["home-dapp__card"]}>
      <Link to={`/dapp_detail/${id}/?tab=${curTab}`}>
        <img className={styles["home-dapp__card-logo"]} alt="dapp" src={logo} />
        <div className={styles["home-dapp__card-info"]}>
          <span className={styles["dapp-name"]}>{name}</span>
          <span className={styles["dapp-price"]}>{`${price} DOS`}</span>
        </div>
      </Link>
      {curTab === 0 && state == 1 && (
        <div className={styles["home-dapp__card-countDown"]}>
          <CountDown time={order_time} now={now} />
        </div>
      )}
      <button
        className={cx({
          [styles.dsb]: curTab === 1,
        })}
        disabled={curTab === 1}
        onClick={() => {
          history.push(`/dapp_buy/${id}`);
        }}
      >
        立即申购
      </button>
    </div>
  );
};

const DappList = ({ loading, list, curTab, history, now }) => {
  if (loading) return <Spin />;
  if (list.length === 0) {
    return (
      <div className={styles["no-data"]}>
        <img src={`${basicURL.imgSrc}/no_data.png`} alt="" />
        <span>暂无相关数据</span>
      </div>
    );
  }
  return (
    <Fragment>
      {list.map((item) => (
        <DappCard
          data={item}
          history={history}
          curTab={curTab}
          noe={now}
          key={item.id.toString()}
        />
      ))}
    </Fragment>
  );
};

function Home(props) {
  const globalStore = useContext(GlobalContext);
  const [curTag, setSelectTag] = useState(-1);
  const [curTab, setTab] = useState(0); // 0:待申购 1:已申购
  const [categories, setCategories] = useState([{ id: -1, name: "全部" }]);
  const [loading, setLoading] = useState(true);
  const [now, setNow] = useNow(0, timer);

  const [devInfo, setDevinfo] = useState(null);
  useEffect(() => {
    fetchDevelopingInfo();
    fetchCategories();
  }, []);

  const [dappList, setDappList] = useState({
    list: [],
    page: 1,
    limit: 10,
    total: 0,
  });
  useEffect(() => {
    fetchList();
  }, [globalStore.wallet_address]);

  const fetchList = async (params) => {
    const _resetParams = {
      addr: globalStore.wallet_address,
      state: 0,
      page: 1,
      limit: 100,
      category: null,
      ...params,
    };

    setLoading(true);
    const { ret, data, time } = await getDappList(_resetParams);
    setNow(time);
    setLoading(false);

    if (!ret || !data) return;

    const newDappList = {
      page: data.page,
      total: data.total,
      list:
        params?.page === 1 ? [...data.list] : [...dappList.list, ...data.list],
    };
    newDappList.list = newDappList.list.map((item) => {
      item.price = item.price / 1e8;
      item.now = time;
      return item;
    });
    console.log(newDappList.list[0]);

    setDappList(newDappList);
  };

  const fetchDevelopingInfo = async () => {
    const { ret, data } = await getDappDevInfo();
    if (ret) {
      setDevinfo({ name: data.name, time: data.time });
    }
    globalStore.updateStore({
      target_address: data.address,
      contract: data.contract,
    });
  };

  const fetchCategories = async () => {
    const { ret, data = [] } = await getCategorites();
    if (!ret || data.length === 0) return;
    setCategories([...categories, ...data]);
  };

  const clickTag = (selectedTag) => {
    if (selectedTag === curTag) return;
    setSelectTag(selectedTag);
    fetchList({ page: 1, category: selectedTag === -1 ? null : selectedTag });
  };

  const switchTab = (tabIndex) => {
    setTab(tabIndex);
    fetchList({ page: 1, category: null, state: parseInt(tabIndex) });
  };

  return (
    <div className={styles.home}>
      <Link className={styles["go-history"]} to="/history">{`申购记录>`}</Link>
      <div className={styles["home-title"]} />
      <div className={styles["home-tab"]}>
        <div className={styles["home-tab__container"]}>
          <div className={styles["home-tab__container-button"]}>
            <button
              className={cx({
                [styles.leftButton]: 1,
                [curTab === 0 ? styles.active : styles.unActive]: 1,
              })}
              onClick={() => switchTab(0)}
            >
              待申购
            </button>
            <button
              className={cx({
                [styles.rightButton]: 1,
                [curTab === 1 ? styles.active : styles.unActive]: 1,
              })}
              onClick={() => switchTab(1)}
            >
              已申购
            </button>
          </div>
          <div className={styles["home-tab__container-tag"]}>
            {categories.map(({ name, id }, i) => (
              <Tag
                className={cx({
                  [styles.active]: id === curTag,
                })}
                key={id.toString()}
                text={name}
                onTap={() => clickTag(id)}
              />
            ))}
          </div>
        </div>
      </div>
      <div className={styles["home-dapp"]}>
        <DevelopNotice data={devInfo} />
        <div className={styles["home-dapp__list"]}>
          <DappList
            {...props}
            curTab={curTab}
            loading={loading}
            list={dappList.list}
            now={now}
          />
        </div>
      </div>
    </div>
  );
}

export default Home;
